import React, { useState, useEffect } from "react";
import { collection, addDoc, serverTimestamp, Firestore } from "firebase/firestore";
import ReCAPTCHA from "react-google-recaptcha";
import emailjs from "emailjs-com";
import { app } from "../../firebaseConfig"; // Asegúrate de que este archivo exporte correctamente `app`

let db: Firestore | null = null;

if (typeof window !== "undefined") {
  const { getFirestore } = require("firebase/firestore"); // Importación condicional
  db = getFirestore(app);
}

// Definimos el tipo de `ErrorState` para evitar el error
type ErrorState = {
  email: string;
  phone: string;
  general: string;
};

const ContactForm: React.FC = () => {
  const [formData, setFormData] = useState({
    fullName: "",
    email: "",
    phone: "",
    message: "",
  });

  const [error, setError] = useState<ErrorState>({
    email: "",
    phone: "",
    general: "",
  });

  const [captchaToken, setCaptchaToken] = useState("");
  const [isClient, setIsClient] = useState(false);

  useEffect(() => {
    // Habilita el renderizado del lado del cliente
    setIsClient(typeof window !== "undefined");
  }, []);

  // Validar campos de correo y teléfono
  const validateEmail = (email: string) =>
    /^[\w-\.]+@([\w-]+\.)+[\w-]{2,4}$/.test(email);
  const validatePhone = (phone: string) =>
    /^\+?\d{10,15}$/.test(phone);

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
    const { name, value } = e.target;
    setFormData({ ...formData, [name]: value });
    setError((prevError) => ({ ...prevError, [name]: "" }));
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();

    // Validación de campos
    let isValid = true;
    const newError: ErrorState = { email: "", phone: "", general: "" };

    if (!validateEmail(formData.email)) {
      newError.email = "Correo electrónico inválido.";
      isValid = false;
    }
    if (!validatePhone(formData.phone)) {
      newError.phone = "Número de teléfono inválido.";
      isValid = false;
    }
    if (!captchaToken) {
      newError.general = "Por favor, completa el reCAPTCHA para enviar el formulario.";
      isValid = false;
    }

    if (!isValid) {
      setError(newError);
      return;
    }

    setError({ email: "", phone: "", general: "" });

    try {
      // Guardar en Firestore solo si `db` está disponible
      if (db) {
        await addDoc(collection(db, "contactForm"), {
          ...formData,
          timestamp: serverTimestamp(),
        });
      }

      // Enviar correo usando EmailJS
      await emailjs.send(
        "service_sm8nf4i", 
        "template_jyqdnxg", 
        {
          to_name: "juanpablo@riano.ca",
          from_name: formData.fullName,
          message: formData.message,
          reply_to: formData.email,
        }, 
        "u2Op-9wm_t5yR4it5"
      );

      setFormData({ fullName: "", email: "", phone: "", message: "" });
      setCaptchaToken("");
    } catch (error) {
      setError((prevError) => ({ ...prevError, general: "Hubo un error al enviar el formulario. Inténtalo nuevamente más tarde." }));
      console.error("Error al enviar el formulario:", error);
    }
  };

  // reCAPTCHA
  const handleReCaptcha = (token: string | null) => {
    if (token) setCaptchaToken(token);
  };

  return (
    <form onSubmit={handleSubmit} className="max-w-lg mx-auto p-4">
      {isClient && (
        <ReCAPTCHA
          sitekey="6Lce2X0qAAAAAGvyv_z_wN8ZvlQB1gvi8NaAcjd7"
          onChange={handleReCaptcha}
        />
      )}
      <div>
        <label>Nombre Completo</label>
        <input
          type="text"
          name="fullName"
          value={formData.fullName}
          onChange={handleInputChange}
          className="w-full p-2 border rounded"
          required
        />
      </div>
      <div>
        <label>Correo Electrónico</label>
        <input
          type="email"
          name="email"
          value={formData.email}
          onChange={handleInputChange}
          className="w-full p-2 border rounded"
          required
        />
        {error.email && <p className="text-red-600">{error.email}</p>}
      </div>
      <div>
        <label>Número de Teléfono</label>
        <input
          type="tel"
          name="phone"
          value={formData.phone}
          onChange={handleInputChange}
          className="w-full p-2 border rounded"
          required
        />
        {error.phone && <p className="text-red-600">{error.phone}</p>}
      </div>
      <div>
        <label>Mensaje</label>
        <textarea
          name="message"
          value={formData.message}
          onChange={handleInputChange}
          className="w-full p-2 border rounded"
          rows={4}
          required
        />
      </div>
      {error.general && <p className="text-red-600">{error.general}</p>}
      <button
        type="submit"
        className="w-full p-2 bg-blue-500 text-white rounded mt-2"
        disabled={!captchaToken}
      >
        Enviar
      </button>
    </form>
  );
};

export default ContactForm;
